// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue/dist/vue.common.js'
import FastClick from 'fastclick'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import axios from 'axios'
import './mock/index.js'


Vue.prototype.$http=axios.create({
  baseURL:'https://cnodejs.org/api/v1',
});


Vue.use(Vuex);
const store = new Vuex.Store({
    state:{
        asst:'',
        loginname:'',
        isLogined:false,  // true false
        isShow:false,  // 
        unLoginedMenus:{  //
          login:'',
          
        },
        isLoginedMenus:{  //
          center:'',
          msg:'',
          logout:'',
        },
        topics:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'test',desc:''}
    
          ]
    },
    getters:{
      moreMenus(state){
        // storestate
        if(state.isLogined){
          return state.isLoginedMenus;
        }
        return state.unLoginedMenus;
      }
    },
    mutations:{ 
        mutationIsshowMore(state,payload){
            state.isShow=payload;
        },
        // 
        mutationIsLogined(state){
          state.isLogined=true
        },
        mutationLoginname(state,payload){
          state.loginname=payload
        },
        mutationsasst(state,payload){
          state.asst=payload
        },
        // 
        mutationLoginout(state){
          state.isLogined=false;
          state.loginname='';
          state.asst='';
        }
    },
    actions:{
      
    }

})



FastClick.attach(document.body)
// vur
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app-box')



